import { Request } from 'express';
import { IOptions } from 'tc-nodejs-core-services/dist/httpClient/IOptions';
import { App } from '../../../App';

export class PackageRequest {
  public uri: string;
  public options: IOptions;

  constructor(request: Request) {
    this.uri = `${App.config.get('papi')}/packages/${request.query.packageId}/matrix`;
    this.options = {
      qs: request.query,
      headers: {
        'content-type': 'application/json',
        'x-correlation-id': request.headers['x-correlation-id'],
        'context': <string> request.headers.context,
        'language': <string> request.headers.language,
      },
      json: true,
    };
  }
}
